<template>
  <div class="inline">
    <div class="time">{{ timer.hours }}</div>horas
    <div class="time">{{ timer.minutes }}</div>minutos
    <div class="time">{{ timer.seconds }}</div>segundos
    <span>({{milliseconds}})</span>
  </div>
</template>

<script>
export default {
  props: {
    milliseconds: {
      type: Number,
      default: 0
    },
    active: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      timerId: null,
      timer: { hours: 0, minutes: 0, seconds: 0 }
    };
  },
  mounted() {
    if (this.active) this.begin();
    this.initialTime();
  },
  methods: {
    begin() {
      this.timerId = setInterval(this.updateTimer, 1000);
    },
    updateTimer() {
      this.timer.seconds++;
      if (this.timer.seconds >= 60) {
        this.timer.minutes++;
        this.timer.seconds = 0;
      }
      if (this.timer.minutes >= 60) {
        this.timer.hours++;
        this.timer.minutes = 0;
      }
    },
    initialTime() {
      // this.milliseconds = 1 * 1 * 59 * 60 * 1000;
      this.timer.hours = parseInt(this.milliseconds / (1000 * 60 * 60));
      this.timer.minutes = parseInt((this.milliseconds / (1000 * 60)) % 60);
      this.timer.seconds = parseInt((this.milliseconds / 1000) % 60);
    },
    pauseTimer() {
      clearInterval(this.timerId);
    }
  },
  watch: {
    active: function(newVal, oldVal) {
      if (this.active === true) this.begin();
      else this.pauseTimer();
    }
  }
};
</script>

<style lang="scss" scoped>
.time {
  display: inline-block;
  margin-right: 0.2em;
  color: var(--v-accent-darken2);
  font-size: 1.5em;
  color: #969696;
}

.inline {
  display: inline-block;
}
</style>
