<template>
  <v-card :class="{ 'selected-card': activity.active }" :outlined="!activity.active">
    <!-- <div
      v-for="lapse in activity.lapses"
      :key="lapse.id"
    >{{ lapse.start | formatDate }} - {{lapse.end | formatDate}}</div>-->
    <v-card-title>{{ activity.name }}</v-card-title>
    <v-container>
      <v-btn
        :disabled="activity.active"
        color="primary"
        class="text-none mr-3"
        @click="beginActivity()"
      >Estoy haciendo esto ahora</v-btn>
      <stop-watch :active="activity.active" :milliseconds="milliseconds"></stop-watch>
    </v-container>
  </v-card>
</template>

<script>
import StopWatch from "@/components/TimeManagment/StopWatch";
import { compareAsc, format } from "date-fns";
import parseISO from "date-fns/parseISO";
import formatISO from "date-fns/formatISO";
import { formatISO9075 } from "date-fns";
import { endOfDay } from "date-fns";
import { getDate } from "date-fns";
import isToday from "date-fns/isToday";
import isYesterday from "date-fns/isYesterday";
import endOfYesterday from "date-fns/endOfYesterday";
export default {
  components: {
    StopWatch
  },
  props: {
    activity: {
      type: Object
    },
    id: {
      type: String
    }
  },
  data() {
    return {
      milliseconds: 0
    };
  },
  filters: {
    formatDate: function(value) {
      return formatISO9075(new Date(value));
    }
  },
  created() {
    this.milliseconds = this.activity.lapses.reduce((prev, curr) => {
      if (!isToday(new Date(curr.start))) {
        if (isYesterday(new Date(curr.start)) && isToday(new Date(curr.end))) {
          return prev + Math.abs(new Date(curr.end) - endOfYesterday());
        }
        return prev + 0;
      }
      return (
        prev +
        Math.abs(
          (curr.end != null ? new Date(curr.end) : new Date()) -
            new Date(curr.start)
        )
      );
    }, 0);
  },
  methods: {
    beginActivity() {
      this.active = true;
      this.$emit("onBeginActivity", this.id);
    },
    calculateMilliseconds(start, end) {
      return Math.abs(
        new Date(start) - (end != null ? new Date(end) : new Date())
      );
    }
  }
};
</script>

<style lang="scss" scoped>
.selected-card {
  border: 1px solid red;
}
</style>
