<template>
  <div class="background">
    <div class="overlay">
      <v-container class="container-max-width">
        <v-card outlined>
          <v-card-title class="primary">
            <!-- <v-icon dark size="30" class="mr-4"></v-icon> -->
            <h2 class="headline white--text">Administración del tiempo</h2>
          </v-card-title>
          <v-container>
            <b v-if="activities.length > 0">
              Actividad activa:
              {{
              activities[
              activities.findIndex(activity => activity._id === prevActiveId)
              ].name || ""
              }}
            </b>
            <v-text-field
              @keyup.enter="addActivity()"
              dense
              outlined
              placeholder="Agrega tu actividad"
              v-model="newActivity.name"
            ></v-text-field>
            <v-text-field
              append-icon="mdi-magnify"
              clearable
              dense
              outlined
              placeholder="Búsqueda de actividad"
              v-model="activityToFind"
            ></v-text-field>
            <activity-component
              v-for="(activity, idx) in filteredList"
              :key="activity.id"
              class="mb-3"
              :activity="activity"
              :id="activity._id"
              @onBeginActivity="onBeginActivity"
            />
          </v-container>
        </v-card>
      </v-container>
    </div>
  </div>
</template>

<script>
import ActivityComponent from "@/components/TimeManagment/ActivityComponent";
import api from "@/services/api/activities";
import axios from "axios";
export default {
  components: {
    ActivityComponent
  },
  data() {
    return {
      activityToFind: "",
      activities: [],
      defaultActivity: { name: "", lapses: [], active: false },
      newActivity: { name: "", lapses: [], active: false },
      prevActiveId: null
    };
  },
  mounted() {
    this.initialData();
  },
  methods: {
    async initialData() {
      await api.fetchActivities().then(res => {
        this.activities = res.data.payload;
        //setting active activity
        this.prevActiveId = this.findActiveActivity(this.activities);
        console.log("el id activo es: ", this.prevActiveId);
      });
    },
    async addActivity() {
      await api
        .createActivity(this.newActivity)
        .then(res => this.activities.push(res.data.payload));
      this.clear();
    },
    clear() {
      this.newActivity = JSON.parse(JSON.stringify(this.defaultActivity));
    },
    async onBeginActivity(activeId) {
      console.log("empezando actividad: ", activeId);
      if (this.prevActiveId != undefined) {
        let prevActiveIndex = this.activities.findIndex(
          activity => activity._id === this.prevActiveId
        );
        console.log("desactivando item: ", this.prevActiveId);
        this.activities[prevActiveIndex].active = false;
        let lastIndex = this.activities[prevActiveIndex].lapses.length - 1;
        this.activities[prevActiveIndex].lapses[lastIndex].end = new Date();
        await api
          .updateActivity(
            this.activities[prevActiveIndex]._id,
            this.activities[prevActiveIndex]
          )
          .then(res => console.log("actualizado prev con exito"));
      }
      this.prevActiveId = activeId;
      console.log("activando item: ", activeId);
      let activeIndex = this.activities.findIndex(
        activity => activity._id === activeId
      );
      this.activities[activeIndex].active = true;
      let date = new Date();
      this.activities[activeIndex].lapses.push({ start: date, end: null });
      await api
        .updateActivity(
          this.activities[activeIndex]._id,
          this.activities[activeIndex]
        )
        .then(res => console.log("actualizado con exito"));
    },
    findActiveActivity(activities) {
      console.log("las actividades son: ", activities);
      return activities.find(activity => activity.active === true)._id;
    }
  },
  computed: {
    filteredList() {
      return this.activities.filter(activity => {
        return (activity.name || "")
          .toLowerCase()
          .includes(this.activityToFind.toLowerCase());
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.overlay {
  background: rgba(48, 51, 107, 0.4);
  height: 100%;
}
.background {
  background-image: url("https://writercarol.com/wp-content/uploads/2017/11/pexels-photo-359989.jpeg");
  height: 100%;
  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: repeat;
  // background-size: cover;
}
.container-max-width {
  max-width: 1000px;
}
</style>
