<template>
  <div>
    <v-navigation-drawer app class="white" v-model="drawer" fixed clipped disable-resize-watcher>
      <v-toolbar flat class="transparent">
        <v-list class="pa-0">
          <v-list-item>
            <v-list-item-avatar>
              <img
                src="data:image/jpeg;base64,"
              />
            </v-list-item-avatar>

            <v-list-item-content>
              <v-list-item-title>Napoleon Hill</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-toolbar>
      <v-divider></v-divider>
      <v-list dense>
        <div v-for="(item, i) in items">
          <v-layout v-if="item.heading" :key="i" row align-center>
            <v-flex xs6>
              <v-subheader>{{ item.heading }}</v-subheader>
            </v-flex>
          </v-layout>

          <v-divider v-else-if="item.divider" :key="i" dark class="my-3"></v-divider>
          <v-list-item v-else :key="i" :to="item.to">
            <v-list-item-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-action>

            <v-list-item-content>
              <v-list-item-title>{{item.text}}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </div>
      </v-list>
    </v-navigation-drawer>

    <v-app-bar color="amber" app fixed>
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>
      <span class="title m1-3 mr-5">
        Master
        <span class="font-weight-light">Mind</span>
      </span>
      <v-text-field
        name="search"
        label="Search"
        id="searchbar"
        flat
        hide-details
        solo-inverted
        prepend-inner-icon="search"
      ></v-text-field>
      <v-spacer></v-spacer>

      <strong>
        Master Points:
        <animated-number
          class="earnedPoints"
          :value="masterPoints"
          :duration="1000"
          :formatValue="formatToMasterPoints"
        />
      </strong>
      <strong class="ml-3">
        Rango:
        <span class="earnedPoints">{{rank[1].name}}</span>
      </strong>
    </v-app-bar>

    <v-content class="white" fluid grid-list-md text-xs-center>
      <router-view></router-view>
    </v-content>
  </div>
</template>

<script>
import { mapState } from "vuex";
import AnimatedNumber from "animated-number-vue";

export default {
  components: {
    AnimatedNumber
  },
  data() {
    return {
      drawer: null,
      items: [
        { icon: "home", text: "Inicio", to: "/" },
        { icon: "lightbulb_outline", text: "Ideas", to: "/ideas" },
        {
          icon: "alarm",
          text: "Administración del tiempo",
          to: "/administracion-del-tiempo"
        },
        {
          icon: "touch_app",
          text: "Objetivos diarios",
          to: "/objetivos-diarios"
        },
        { divider: true },
        { heading: "Labels" },
        { icon: "add", text: "Create new label" },
        { divider: true },
        { icon: "archive", text: "Archive" },
        { icon: "delete", text: "Trash" },
        { divider: true },
        { icon: "settings", text: "Settings" },
        { icon: "chat_bubble", text: "Trash" },
        { icon: "help", text: "Help" },
        { icon: "phonelink", text: "App downloads" },
        { icon: "keyboard", text: "Keyboard shortcuts" }
      ]
    };
  },
  computed: {
    ...mapState(["rank", "masterPoints"])
  },
  methods: {
    formatToMasterPoints(value) {
      return `${value.toFixed(0)}mp`;
    }
  }
};
</script>

<style lang="scss">
#keep main .container {
  height: 660px;
}

.navigation-drawer__border {
  display: none;
}

.text {
  font-weight: 400;
}
</style>